.create{
    color:#555;
    max-width: 480px;
    margin:60px auto;

} 
.create label span{
    display:block;
    margin:30px 0 10px;
}
.create p{
    margin-top:10px;
    font-size:0.8em;

}
.create button{
    display: block;
    width:150px;
    font-size: 1em;
    color:#fff;
    padding:10px 20px;
    border:0;
    border-radius:4px;
    background-color:#58249c;
    cursor:pointer;
    text-decoration:none;
    margin:20px auto;

}