.recipe-list{
    display: grid;
    grid-template-columns:1fr 1fr;
    grid-gap:60px;
    max-width: 1200px;
    margin: 40px auto;
}

.recipe-list .card{
    background: #333;
    padding:20px;
    color:#333;
    border-radius: 4px;
    box-shadow:3px 3px 3px rgba(0,0,0,0.05);
    transition: all 0.3s ease;
    position: relative;
}


.recipe-list .card:hover{
    transform: rotate(3deg);

}
.recipe-list .card h3{
color:#555;
margin-bottom: 6px;
}
.recipe-list .card p{
    color:#999;
    font-size: 0.9em;
}
.recipe-list .card div{
    color:#555;
    font-size:0.7em;
    margin: 20px 0;
    line-height: 1.5em;

}
.recipe-list .card a{
    color:#555;
    text-decoration: none;
    display: block;
    background: #e2e2e2;
    font-size: 0.9em;
    text-align: center;
    width: 120px;
    padding:8px;
    border-radius:4px;
    margin:20px auto 0;
}